.field-label {
    color: $tenda-color;
}

.brand {
    display: none;
}
.navbar-text {
    display: none;
}

.navbar-login img {
    height: 26px;
}

.exit {
    padding-top:10px;
}

.list-group-item {
    color: $tenda-color;
    background: url(/w20e/common/img/sidebar_active_tenda.png) #f5f5f5 no-repeat
}

#bar1, #bar2, #bar3, #bar4{
    color: $tenda-color;
}

.btn-primary {
    background-color: $tenda-color;
    border-color: $tenda-color;
}

.btn-primary:hover,
.btn-primary:focus,
.btn-primary:active,
.open > .dropdown-toggle.btn-primary {
    color: #ffffff;
    background-color: $tenda-color;
    border-color:$tenda-color;
}

.menu-group a.active {
    background: $tenda-color;
}

#tendaWebsite a:hover,
#tendaWebsite a:focus,
#tendaWebsite a:active {
    color: $tenda-color;
}
#tendaAuthWebsite a:hover,
#tendaAuthWebsite a:focus,
#tendaAuthWebsite a:active {
    color: $tenda-color;
}

#tendaLoginWebsite a:hover,
#tendaLoginWebsite a:focus,
#tendaLoginWebsite a:active {
    color: $tenda-color;
}

#showAuthPage,
#showAuthPage:hover,
#showAuthPage:focus,
#showAuthPage:active {
    color: $tenda-color;
}

#wewifiHelp,
#wewifiHelp:hover,
#wewifiHelp:focus,
#wewifiHelp:active {
    color: $tenda-color;
}

#wewifiBack,
#wewifiBack:hover,
#wewifiBack:focus,
#wewifiBack:active {
    color: $tenda-color;
}


.self-select-wrapper .self-select-option:hover{
    background-color: $tenda-color;
}

#ddnsContanier a, #sysInfo a ,
#cloudDetail a{
    color: #555555;
}
#ddnsContanier a:hover, #sysInfo a:hover, 
#sysInfo a:focus, #cloudDetail a:hover {
    color: $hilight-color;
}

#sysInfo li.active{
    a {
        color: $tenda-color;
    }
}

.progress-bar {
    background-color: $hilight-color;
}

.refresh-icon {
    background: url(/w20e/common/img/refresh-tenda.png) center no-repeat;
}
.refresh-icon.refreshing {
    background: url(/w20e/common/img/refreshing-tenda.gif) center no-repeat;
}

